<template>
  <div id="search-menu">
    <input
      v-if="this.chosen_artists.length < 5"
      v-model="input_content"
      v-on:keyup="searchArtists"
      placeholder="Search by Artist"
    />
    <input v-else placeholder="Maximum amount of Artists" disabled />
    <div v-if="this.input_content != ''" class="search-suggestions">
      <ul>
        <li v-for="name in search_suggestions" :key="name.id">
          <span v-on:click="addArtist(name)">{{ name.name }}</span>
        </li>
      </ul>
    </div>

    <div v-if="chosen_artists.length > 0" id="current_artists">
      <ul>
        <li v-for="artist in chosen_artists" :key="artist.id">
          {{ artist.name }}
          <span
            class="remove-artist"
            v-on:click="removeArtist(artist.id)"
            style=""
            >&#10005;</span
          >
        </li>
      </ul>
    </div>
    <br />
  </div>
</template>
<script>
import Vue from "vue";
export default {
  props: [""],
  name: "searchArtists",
  components: {},
  created() {
    this.getAccessToken();
    if (this.accessToken != null) {
      console.log("created");
    } else {
      console.log("ooooof");
    }
  },
  data() {
    return {
      accessToken: null,
      input_content: "",
      search_suggestions: [],
      chosen_artists: [],
    };
  },
  methods: {
    getAccessToken: function() {
      let self = this;
      var hash = window.location.hash.substring(1);
      var accessString = hash.indexOf("&");
      /* 13 because that bypasses 'access_token' string */
      self.accessToken = hash.substring(13, accessString);
    },
    searchArtists: function() {
      var self = this;
      if (self.input_content != "") {
        Vue.axios
          .get(
            `https://api.spotify.com/v1/search?q=${self.input_content}&type=artist`,
            {
              headers: {
                Authorization: "Bearer " + self.accessToken,
              },
            }
          )
          .then((response) => {
            self.search_suggestions = response.data.artists.items;
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    addArtist: function(name) {
      var self = this;
      var found = false;
      for (var i = 0; i < self.chosen_artists.length; i++) {
        if (self.chosen_artists[i].name == name.name) {
          found = true;
          break;
        }
      }
      if (self.chosen_artists.length < 5) {
        if (!found) {
          self.chosen_artists.push(name);
          self.input_content = "";
          console.log(self.chosen_artists);
        } else {
          self.input_content = "";

          console.log("no duplicates");
        }
      }
    },
    removeArtist: function(id) {
      var self = this;
      self.chosen_artists.splice(id, 1);
    },
  },
};
</script>
<style scoped>
#search-menu {
  float: left;
  width: 100%;
}
input {
  width: 100%;
  height: 30px;
  font-size: 20px;
}
.search-suggestions {
  position: absolute;
  z-index: 2;
  width: 100%;
  background-color: white;
}
.search-suggestions li {
  list-style-type: none;
  width: 100%;
  height: 20px;
  text-align: left;
}
.search-suggestions li:hover {
  background-color: lightgray;
}
.search-suggestions ul {
  float: left;
  width: 100%;
  height: 100px;
  overflow: hidden;
  overflow-y: scroll;
}
#current_artists li {
  list-style-type: none;
  float: left;
  margin-right: 10px;
  border: 1px green solid;
  background-color: green;
  color: white;
  padding: 5px;
}
div {
  color: black;
}
.remove-artist {
  cursor: pointer;
}
</style>
